import Close from '../../assets/icons/Close'

export interface ChipGroupProps {
  fieldName: any
  watch: any
  register: any
  label?: string
  options: any[]
  errors: any
  maxLength?: number
}

export const ChipGroup = ({
  fieldName,
  watch,
  register,
  label,
  options,
  errors,
  maxLength = 3,
}: ChipGroupProps) => {
  const validate = (value: any[]) => {
    if (value.length > maxLength) {
      return `請最多選擇${maxLength}個`
    }
    return true
  }
  return (
    <div>
      <div className="mb-2 text-graphite">{label}</div>
      <div className="flex flex-wrap gap-2">
        {options.map((option) => {
          const isChecked = watch(fieldName)?.includes(option)
          return (
            <span
              key={option}
              className={`cursor-pointer rounded-full border border-black px-2 py-1 
                    ${isChecked ? 'bg-black text-white' : ''}`}
            >
              <input
                {...register(fieldName, { validate })}
                className="hidden"
                id={option}
                value={option}
                type="checkbox"
              />
              <label
                className="flex cursor-pointer items-center gap-1"
                htmlFor={option}
              >
                {isChecked && <Close className="h-4 w-4 fill-white" />}
                {option}
              </label>
            </span>
          )
        })}
      </div>
      {errors[fieldName]?.type === 'validate' && (
        <div className="text-small-title mt-2 w-full bg-[#fdefe5] p-1 text-[#F05F00]">
          {errors[fieldName]?.message}
        </div>
      )}
    </div>
  )
}
