const OpeningAnimation = () => {
  const [index, setIndex] = useState<number>(0)
  const isIntroSlideshowLoaded =
    sessionStorage.getItem('introSlideshow') === 'loaded'

  useEffect(() => {
    const interval = setInterval(() => {
      if (index >= svgList.length) {
        clearInterval(interval)
      } else {
        setIndex((i) => i + 1)
      }
    }, 1000)

    return () => clearInterval(interval)
  }, [index])

  if (isIntroSlideshowLoaded) {
    return null
  }

  return (
    <AnimatePresence>
      {index < svgList.length && (
        <motion.div
          className="absolute left-0 top-0 z-10 h-full w-full bg-white"
          initial="hidden"
          animate="visible"
          exit="hidden"
        >
          <AnimatePresence>
            {index === 0 && (
              <motion.div
                className="grid h-full place-content-start"
                initial="hidden"
                animate="visible"
                exit="hidden"
                variants={svgVariants}
              >
                {svgList[0]}
              </motion.div>
            )}
          </AnimatePresence>
          <AnimatePresence>
            {index === 1 && (
              <motion.div
                className="grid h-full place-items-center"
                initial="hidden"
                animate="visible"
                exit="hidden"
                variants={svgVariants}
              >
                {svgList[1]}
              </motion.div>
            )}
          </AnimatePresence>
          <AnimatePresence>
            {index === 2 && (
              <motion.div
                className="grid h-full place-items-end"
                initial="hidden"
                animate="visible"
                exit="hidden"
                variants={svgVariants}
                onAnimationComplete={() => {
                  sessionStorage.setItem('introSlideshow', 'loaded')
                }}
              >
                {svgList[2]}
              </motion.div>
            )}
          </AnimatePresence>
          {index < svgList.length && (
            <button
              className="fixed bottom-6 right-6 z-20 bg-white"
              type="button"
              onClick={() => {
                setIndex(svgList.length)
                sessionStorage.setItem('introSlideshow', 'loaded')
              }}
            >
              skip
            </button>
          )}
        </motion.div>
      )}
    </AnimatePresence>
  )
}

export default OpeningAnimation
