import React, { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import { Screenplay } from '../../typings'
import ScreenplayForm from '../../components/form/Screenplay'
import AppContext from '../../context/AppContext'
import AuthContext from '../../context/AuthContext'

const EditPage = () => {
  const { fetchScreenplay } = useContext(AppContext)
  const { authUser } = useContext(AuthContext)
  const [screenplay, setScreenplay] = useState<null | Screenplay>(null)
  const { screenplayId } = useParams()

  useEffect(() => {
    if (screenplayId === undefined) {
      setScreenplay({
        usernames: [authUser?.username].filter((v) => v) as string[],
        name: '',
        outline: '',
        logline: '',
        videoUrl: '',
        isDeleted: 0,
        script: [],
        characters: [],
        confession: '',
        types: [],
        languages: [],
        fileId: '',
        likes: 0,
        isLiked: false,
        createdAt: 0,
        updatedAt: 0,
      })
    } else {
      fetchScreenplay(screenplayId ?? '').then((v) => {
        setScreenplay(v)
      })
    }
  }, [authUser?.username, fetchScreenplay, screenplayId])

  if (screenplay === null) {
    return <>Loading</>
  }

  return <ScreenplayForm screenplay={screenplay} />
}

export default EditPage
