import { useContext, useState } from 'react'
import { useQuery } from '@tanstack/react-query'
import { NavLink, useSearchParams } from 'react-router-dom'
import { motion } from 'framer-motion'
import AppContext from '../../context/AppContext'
import { ScreenType, ScreenplayEntry } from '../../typings'
import ListRow from '../List/ListRow'
import Favorite from '../../favorite.svg'
import Favorited from '../../favorited.svg'
import Paginate from '../Paginate/Paginate'
import SubHeader from '../SubHeader/SubHeader'

interface ScreenplayListProps {
  itemsPerPage: number
  username?: string
}

const ScreenplayList = ({ itemsPerPage, username }: ScreenplayListProps) => {
  const { fetchScreenplayList } = useContext(AppContext)
  const [searchParams, setSearchParams] = useSearchParams()
  const [currentPage, setCurrentPage] = useState(
    parseInt(searchParams.get('page') || '1', 10)
  )
  const [currentType, setCurrentType] = useState(searchParams.get('type') || '')

  const paginate = (pageNumber: number) => {
    setCurrentPage(pageNumber)
    searchParams.set('page', pageNumber.toString())

    setSearchParams({
      page: pageNumber.toString(),
      type: searchParams.get('type') || '',
    })
  }

  const handleOptionChange = (event: any) => {
    const newType = event.target.value
    setCurrentType(newType)
    searchParams.set('type', newType)

    // Always back to page 1
    paginate(1)
  }

  const isComponentAnimationLoaded =
    sessionStorage.getItem('componentAnimation') === 'loaded'

  const { isLoading, isError, error, data, isPreviousData } = useQuery({
    queryKey: ['screenplays', currentPage, currentType],
    queryFn: () =>
      fetchScreenplayList(
        currentPage - 1,
        itemsPerPage,
        username || '',
        currentType
      ),
    keepPreviousData: true,
  })

  if (!data) {
    return <>Loading.</>
  }

  return (
    <>
      <SubHeader
        breadcrumbTrail={['SCREENPLAY_LIST']}
        secondaryAction={
          <div>
            <label className="cursor-pointer" htmlFor="types">
              顯示:
            </label>
            <select
              className="cursor-pointer"
              name="types"
              id="types"
              onChange={handleOptionChange}
            >
              <option value="">所有</option>
              {(
                [
                  '喜劇',
                  '愛情',
                  '動作',
                  '科幻',
                  '動畫',
                  '懸疑',
                  '犯罪',
                  '驚悚',
                  '冒險',
                  '音樂',
                  '歷史',
                  '奇幻',
                  '恐怖',
                  '戰爭',
                  '傳記',
                  '歌舞',
                  '武俠',
                  '情色',
                  '災難',
                  '西部',
                  '短片',
                  '劇情',
                ] as ScreenType[]
              ).map((screenType) => (
                <option key={screenType} value={screenType}>
                  {screenType}
                </option>
              ))}
            </select>
          </div>
        }
      />
      <motion.div
        className="hidden gap-x-4 border-y border-graphite py-2 md:grid md:grid-cols-12"
        initial={{ opacity: 0 }}
        animate={{ opacity: 100 }}
        transition={{ duration: 1.25, ease: 'easeOut', delay: 0.75 }}
      >
        <span className="text-small-title pl-4 md:col-span-2 xl:col-span-1">
          排行
        </span>
        <span className="text-small-title md:col-span-2 xl:col-span-4">
          劇名
        </span>
        <span className="text-small-title md:col-span-2 xl:col-span-4">
          LOGLINE
        </span>
        <span className="text-small-title md:col-span-2 xl:col-span-1">
          支持者(名)
        </span>
        <span className="text-small-title text-right md:col-span-2 xl:col-span-1">
          類型
        </span>
        <span className="text-small-title pr-4 text-right md:col-span-2 xl:col-span-1">
          編劇
        </span>
      </motion.div>
      <motion.figure
        className="block h-px w-full origin-left bg-black"
        initial={{ scaleX: 0 }}
        animate={{ scaleX: 1 }}
        transition={{ duration: 1.25, ease: 'easeInOut', delay: 0.5 }}
      />
      <div>
        {isLoading ? (
          <div>Loading...</div>
        ) : isError ? (
          <div>Error: {(error as any).message}</div>
        ) : (
          <ul>
            {data.data?.map(
              (
                {
                  id: screenplayId,
                  rank,
                  name,
                  screenwriters,
                  types,
                  isLiked,
                  likes,
                  logline,
                }: ScreenplayEntry,
                i
              ) => (
                <ListRow
                  key={screenplayId}
                  initial={!isComponentAnimationLoaded}
                  contentTransition={{
                    duration: 1.25,
                    ease: 'easeOut',
                    delay: 1 + (i % itemsPerPage) * 0.25,
                  }}
                  lineTransition={{
                    duration: 1.25,
                    ease: 'easeInOut',
                    delay: 0.75 + (i % itemsPerPage) * 0.25,
                  }}
                >
                  <NavLink
                    to={`/screenplays/${screenplayId}`}
                    className="grid cursor-pointer items-center gap-x-4 border-b border-graphite p-4 md:grid-cols-12 md:grid-rows-[156px] md:py-0 lg:grid-rows-[128px] xl:grid-rows-[106px]"
                  >
                    <span className="h-full place-content-center border-r border-graphite font-nanum-pen text-3xl md:col-span-2 md:grid xl:col-span-1">
                      {rank}
                    </span>
                    <span className="text-title grid h-full items-center md:col-span-2 xl:col-span-4">
                      {name}
                    </span>
                    <span className="text-small-title md:col-span-2 xl:col-span-4">
                      {logline}
                    </span>
                    <span className="text-small-title grid h-full items-center md:col-span-2 xl:col-span-1">
                      <span className="text-small-title flex items-center gap-2">
                        {isLiked ? (
                          <img src={Favorited} alt="已點讚" />
                        ) : (
                          <img src={Favorite} alt="點讚" />
                        )}{' '}
                        {likes}
                      </span>
                    </span>
                    <span className="text-small-title grid h-full items-center text-right md:col-span-2 xl:col-span-1">
                      {types.map((type) => type).join('丶')}
                    </span>
                    <span className="text-small-title grid h-full items-center text-right md:col-span-2 xl:col-span-1">
                      {screenwriters
                        .map(({ username: writerUsername }) => writerUsername)
                        .join(', ')}
                    </span>
                  </NavLink>
                </ListRow>
              )
            )}
          </ul>
        )}
      </div>
      <Paginate
        itemsPerPage={itemsPerPage}
        currentPage={currentPage}
        totalCount={data?.totalCount}
        paginate={paginate}
        isPreviousData={isPreviousData}
      />
    </>
  )
}

export default ScreenplayList
