import React, { useState } from 'react'
import { NavLink } from 'react-router-dom'
import { motion } from 'framer-motion'
import { RoutePaths, RouteNames } from '../../enums/routeEnum'
import Logo from '../../logo.svg'
import Menu from '../../assets/icons/Menu'
import Close from '../../assets/icons/Close'
import UserDropdown from './UserDropdown'

const SiteLogo = () => (
  <h1>
    <NavLink to="/">
      <img className="h-[40px] md:h-[60px]" src={Logo} alt="三場戲" />
    </NavLink>
  </h1>
)

const NAV_LINKS = ['SCREENPLAY_LIST', 'EVENTS', 'ABOUT', 'SCREENWRITER_LIST']

type RouteStrings = keyof typeof RouteNames

const TabletMenu = () => {
  const isComponentAnimationLoaded =
    sessionStorage.getItem('componentAnimation') === 'loaded'
  return (
    <ul className="flex items-center">
      {NAV_LINKS.map((to, i) => (
        <li key={to}>
          <NavLink
            to={`/${RoutePaths[to as RouteStrings]}`}
            className="text-small-title group block"
          >
            {({ isActive }) => (
              <motion.div
                initial={
                  isComponentAnimationLoaded ? false : { opacity: 0, y: '100%' }
                }
                animate={{ opacity: 1, y: 0 }}
                transition={{
                  duration: 0.5,
                  delay: 1.1 + 0.25 * (i + 1),
                  ease: 'easeOut',
                }}
                className={`bg-contain bg-center bg-no-repeat px-6 pt-3 pb-1 hover:bg-stroke01 ${
                  isActive && 'bg-stroke01'
                }`}
              >
                {RouteNames[to as RouteStrings]}
              </motion.div>
            )}
          </NavLink>
        </li>
      ))}
      <li>
        <UserDropdown />
      </li>
    </ul>
  )
}

const MobileMenu = () => {
  const [showMenu, setShowMenu] = useState(false)
  const isComponentAnimationLoaded =
    sessionStorage.getItem('componentAnimation') === 'loaded'

  return (
    <>
      <div onClick={() => setShowMenu(true)}>
        <Menu className="h-4 w-4 cursor-pointer fill-black" />
      </div>
      {showMenu && (
        <nav className="absolute top-0 left-0 z-10 h-full w-full bg-white">
          <div className="flex h-[72px] content-center items-center justify-between px-4">
            <SiteLogo />
            <div onClick={() => setShowMenu(false)}>
              <Close className="h-4 w-4 cursor-pointer fill-black" />
            </div>
          </div>
          <ul className="mt-10 flex flex-col gap-2">
            {NAV_LINKS.map((to, i) => (
              <li key={to}>
                <NavLink
                  to={`/${RoutePaths[to as RouteStrings]}`}
                  className="text-heading"
                >
                  {({ isActive }) => (
                    <motion.div
                      initial={
                        isComponentAnimationLoaded
                          ? false
                          : { opacity: 0, y: '100%' }
                      }
                      animate={{ opacity: 1, y: 0 }}
                      transition={{
                        duration: 0.5,
                        delay: 1.1 + 0.25 * (i + 1),
                        ease: 'easeOut',
                      }}
                      className={`inline-block bg-contain bg-center bg-no-repeat pl-6 pr-8 pt-6 pb-4 hover:bg-stroke01 ${
                        isActive ? 'bg-stroke01' : ''
                      }`}
                      onClick={() => setShowMenu(false)}
                    >
                      {RouteNames[to as RouteStrings]}
                    </motion.div>
                  )}
                </NavLink>
              </li>
            ))}
            <li>
              <UserDropdown />
            </li>
          </ul>
        </nav>
      )}
    </>
  )
}
const Header = () => {
  const isComponentAnimationLoaded =
    sessionStorage.getItem('componentAnimation') === 'loaded'

  return (
    <header>
      <div className="flex h-[72px] content-center items-center justify-between bg-white px-4 md:h-[103px]">
        <motion.div
          initial={
            isComponentAnimationLoaded ? false : { opacity: 0, y: '100%' }
          }
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1, duration: 1.25, ease: 'easeOut' }}
          onAnimationComplete={() => {
            sessionStorage.setItem('componentAnimation', 'loaded')
          }}
        >
          <SiteLogo />
        </motion.div>
        <div className="hidden md:block">
          <TabletMenu />
        </div>
        <div className="md:hidden">
          <MobileMenu />
        </div>
      </div>
      <motion.figure
        className="block h-px w-full origin-left bg-black"
        initial={isComponentAnimationLoaded ? false : { scaleX: 0 }}
        animate={{ scaleX: 1 }}
        transition={{ duration: 1.25, ease: 'easeInOut' }}
      />
    </header>
  )
}

export default Header
