import { motion } from 'framer-motion'
import { Breadcrumb } from './Breadcrumb'

interface SubHeaderProps {
  breadcrumbTrail?: string[]
  secondaryAction?: React.ReactNode
}

const SubHeader = ({ breadcrumbTrail, secondaryAction }: SubHeaderProps) => {
  const isComponentAnimationLoaded =
    sessionStorage.getItem('componentAnimation') === 'loaded'

  return (
    <section>
      <motion.div
        className="text-small-title flex items-center justify-between p-4"
        initial={isComponentAnimationLoaded ? false : { opacity: 0 }}
        animate={{ opacity: 100 }}
        transition={{ duration: 1.25, ease: 'easeOut', delay: 0.75 }}
      >
        <Breadcrumb breadcrumbTrail={breadcrumbTrail} />
        {secondaryAction}
      </motion.div>
      <motion.figure
        className="h-px w-full origin-left bg-black md:block"
        initial={isComponentAnimationLoaded ? false : { scaleX: 0 }}
        animate={{ scaleX: 1 }}
        transition={{ duration: 1.25, ease: 'easeInOut', delay: 0.5 }}
      />
    </section>
  )
}

export default SubHeader
