import { useContext, useEffect, useState } from 'react'
import { useParams } from 'react-router-dom'
import ReactMarkdown from 'react-markdown'
import AppContext from '../../context/AppContext'
import { Event } from '../../typings'
import Stroke03 from '../../assets/strokes/Stroke03'
import SubHeader from '../../components/SubHeader/SubHeader'

const fetchedImgSrc = 'https://source.unsplash.com/random/?city,night'

const EventPage = () => {
  const { eventId } = useParams()
  const { fetchEvent } = useContext(AppContext)
  const [event, setEvent] = useState<null | Event>(null)

  useEffect(() => {
    fetchEvent(parseInt(eventId ?? '', 10)).then((v) => {
      setEvent(v)
    })
  }, [fetchEvent, setEvent, eventId])

  if (event === null) {
    return <>Loading...</>
  }
  return (
    <>
      <SubHeader breadcrumbTrail={['EVENTS', 'EVENT_DETAIL']} />
      <section className="flex flex-col justify-between border-b border-graphite p-4 md:flex-row md:items-center">
        <h1 className="text-heading">{event.name}</h1>
        <button
          className="text-heading mt-4 flex items-center gap-x-1 bg-highlight01 bg-contain bg-no-repeat md:mt-0"
          type="button"
        >
          <Stroke03 className="h-8 xl:h-10" />
          立即投稿
        </button>
      </section>
      <div className="flex h-[500px] w-full justify-center bg-black">
        <div
          className="aspect-video max-h-[500px] bg-black"
          style={{ backgroundImage: `url(${fetchedImgSrc})` }}
        />
      </div>
      {event.content && (
        <div className="grid grid-cols-12 px-4 py-10">
          <div className="col-span-12 max-w-screen-lg md:col-span-9 lg:col-span-8">
            <ReactMarkdown className="markdown-preview">
              {event.content}
            </ReactMarkdown>
          </div>
        </div>
      )}
    </>
  )
}

export default EventPage
