import { useState, useContext, useEffect } from 'react'
import { useNavigate } from 'react-router-dom'
import {
  useForm,
  SubmitHandler,
  useFieldArray,
  UseFormWatch,
  UseFormRegister,
  Controller,
} from 'react-hook-form'
import { ScreenLanguage, ScreenType, Screenplay } from '../../typings'
import AppContext from '../../context/AppContext'
import MarkdownEditor from '../MarkdownEditor/MarkdownEditor'
import { TextArea, TextAreaProps } from './TextArea'
import { ChipGroup } from './ChipGroup'
import AddButton from './AddButton'
import RemoveButton from './RemoveButton'
import AuthContext from '../../context/AuthContext'
import SubHeader from '../SubHeader/SubHeader'

interface ScreenplayWithFileFormProps extends Screenplay {
  files?: FileList
}
interface ScreenplayTextAreaProps extends TextAreaProps {
  fieldName: keyof Screenplay
  watch: UseFormWatch<Screenplay>
  register: UseFormRegister<Screenplay>
}
interface ScreenplayFormProps {
  screenplay: Screenplay
}

const ScreenplayTextArea = ({
  fieldName,
  watch,
  register,
  label,
  hintText,
  errors,
  maxLength,
}: ScreenplayTextAreaProps) => (
  <TextArea
    fieldName={fieldName}
    label={label}
    hintText={hintText}
    register={register}
    watch={watch}
    errors={errors}
    maxLength={maxLength}
  />
)

const ScreenplayForm = ({ screenplay }: ScreenplayFormProps) => {
  const [submittedScreenplayId, setSubmittedScreenplayId] = useState<
    string | null
  >(null)
  const { authUser } = useContext(AuthContext)
  const { saveScreenplay, uploadFile } = useContext(AppContext)
  const navigate = useNavigate()
  const [fileObj, setFileObj] = useState<File | null>(null)

  const {
    register,
    control,
    formState: { errors },
    handleSubmit,
    watch,
  } = useForm<ScreenplayWithFileFormProps>({
    mode: 'onChange',
    defaultValues: screenplay,
  })

  const {
    fields: characterFields,
    append: appendCharacter,
    remove: removeCharacter,
  } = useFieldArray({
    control,
    name: 'characters',
  })

  const {
    fields: scriptFields,
    append: appendScript,
    remove: removeScript,
  } = useFieldArray({
    control,
    name: 'script',
  })

  // submitted
  useEffect(() => {
    if (submittedScreenplayId) {
      setTimeout(() => {
        navigate(`/screenplays/${submittedScreenplayId}`)
      }, 1500)
    }
  }, [submittedScreenplayId])

  const onSubmit: SubmitHandler<ScreenplayWithFileFormProps> = (data) => {
    const postData = () => {
      const screenplayData = {
        ...data,
        id: screenplay.id || null,
        usernames:
          Array.isArray(screenplay.screenwriters) &&
          screenplay.screenwriters.length
            ? screenplay.screenwriters.map(
                (screenwriter) => screenwriter.username
              )
            : [authUser!.username],
      }

      saveScreenplay(screenplayData).then((screenplayId) => {
        if (screenplayId) {
          setSubmittedScreenplayId(screenplayId)
        }
      })
    }

    // Upload Screenplay PDF
    if (data.files?.length) {
      const formData = new FormData()
      formData.append('file', data.files[0])
      formData.append('name', data.files[0].name)
      uploadFile(formData).then((file) => {
        if (file) {
          data.fileId = file.id
          postData()
        }
      })
    } else {
      postData()
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <SubHeader
        breadcrumbTrail={['SCREENPLAY_EDIT']}
        secondaryAction={
          <input
            className="cursor-pointer rounded-full border bg-black px-8 py-2 text-white"
            type="submit"
            value="儲存並發布"
          />
        }
      />
      <section className="m-4 grid grid-cols-12">
        <div className="col-span-12 flex max-w-screen-lg flex-col gap-10 md:col-span-9 lg:col-span-8">
          <div>
            <input
              className="text-heading	w-full font-light focus:outline-none"
              placeholder="作品名"
              {...register('name', {
                required: true,
                maxLength: 15,
              })}
            />
            <span className="text-small-title">
              {watch('name')?.length} / 15
            </span>
            {errors.name?.type === 'maxLength' && (
              <div className="text-small-title mt-2 w-full bg-[#fdefe5] p-1 text-[#F05F00]">
                請最多輸入15字
              </div>
            )}
          </div>

          <ScreenplayTextArea
            fieldName="logline"
            label="Logline"
            hintText="撮要顯示在排行榜"
            register={register}
            watch={watch}
            errors={errors}
            maxLength={50}
          />

          <ScreenplayTextArea
            fieldName="outline"
            label="故事大綱"
            register={register}
            watch={watch}
            errors={errors}
            maxLength={300}
          />

          <section>
            <div className="mt-10 mb-2 text-graphite">人物簡介</div>
            {characterFields.map(({ role, actor, description }, i) => (
              <div
                key={`${role}-${actor}-${description}`}
                className="mb-2 flex flex-col gap-4 rounded-md border border-grey2 bg-grey2 p-4"
              >
                <ScreenplayTextArea
                  fieldName={`characters.${i}.role` as keyof Screenplay}
                  hintText="角色"
                  register={register}
                  watch={watch}
                  errors={errors}
                  maxLength={250}
                />
                <ScreenplayTextArea
                  fieldName={`characters.${i}.actor` as keyof Screenplay}
                  hintText="首選演員"
                  register={register}
                  watch={watch}
                  errors={errors}
                  maxLength={250}
                />
                <ScreenplayTextArea
                  fieldName={`characters.${i}.description` as keyof Screenplay}
                  hintText="描述"
                  register={register}
                  watch={watch}
                  errors={errors}
                  maxLength={250}
                />
                <RemoveButton
                  className="mt-4"
                  onClick={() => removeCharacter(i)}
                  text="刪除人物"
                />
              </div>
            ))}
            <div className="text-small-title mt-2 rounded-md	border border-dashed border-graphite p-4">
              <AddButton
                onClick={() => {
                  appendCharacter({
                    role: '',
                    actor: '',
                    description: '',
                  })
                }}
                text="新增人物"
              />
            </div>
          </section>

          <section>
            <div className="mt-10 mb-2 text-graphite">劇本</div>
            {scriptFields.map(({ session, time, scene }, i) => (
              <div
                key={`${session}-${time}-${scene}`}
                className="mb-2 flex flex-col gap-4 rounded-md border border-grey2 bg-grey2 p-4"
              >
                <ScreenplayTextArea
                  fieldName={`script.${i}.session` as keyof Screenplay}
                  hintText="場次"
                  register={register}
                  watch={watch}
                  errors={errors}
                  maxLength={250}
                />
                <ScreenplayTextArea
                  fieldName={`script.${i}.time` as keyof Screenplay}
                  hintText="時間"
                  register={register}
                  watch={watch}
                  errors={errors}
                  maxLength={250}
                />
                <ScreenplayTextArea
                  fieldName={`script.${i}.scene` as keyof Screenplay}
                  hintText="場景"
                  register={register}
                  watch={watch}
                  errors={errors}
                  maxLength={250}
                />
                <MarkdownEditor
                  fieldName={`script.${i}.content` as keyof Screenplay}
                  register={register}
                  watch={watch}
                />
                <RemoveButton onClick={() => removeScript(i)} text="刪除劇本" />
              </div>
            ))}
            <div className="text-small-title mt-2 rounded-md border border-dashed border-graphite p-4">
              <AddButton
                onClick={() => {
                  appendScript({
                    session: '',
                    time: '',
                    scene: '',
                    content: '',
                  })
                }}
                text="新增劇本"
              />
            </div>
          </section>

          <ScreenplayTextArea
            fieldName="confession"
            label="編劇自白"
            register={register}
            watch={watch}
            errors={errors}
            maxLength={300}
          />

          <ScreenplayTextArea
            fieldName="videoUrl"
            label="影片"
            hintText="Valid YouTube link"
            register={register}
            watch={watch}
            errors={errors}
            maxLength={300}
          />

          <Controller
            name="files"
            control={control}
            render={({ field }) => (
              <div className="mt-10">
                <label className="mb-2 block text-graphite">劇本上載</label>
                <div className="mb-2 rounded-md border border-grey2 bg-grey2 p-4">
                  <p className="text-small-title mb-4 text-graphite">
                    PDF檔案，10MB以下
                  </p>
                  <AddButton
                    htmlFor="file-input"
                    text={fileObj ? fileObj.name : '新增'}
                  />
                  <input
                    accept=".pdf"
                    id="file-input"
                    hidden
                    type="file"
                    onChange={(e) => {
                      field.onChange(e.target.files)
                      const file = e.target.files && e.target.files[0]
                      if (file) {
                        setFileObj(file)
                      }
                    }}
                  />
                </div>
              </div>
            )}
          />

          <section>
            <ChipGroup
              label="語言 +"
              watch={watch}
              fieldName="languages"
              register={register}
              options={
                ['粵語', '國語', '台語', '英語', '其他'] as ScreenLanguage[]
              }
              errors={errors}
            />

            <ChipGroup
              label="類型 +"
              watch={watch}
              fieldName="types"
              register={register}
              options={
                [
                  '喜劇',
                  '愛情',
                  '動作',
                  '科幻',
                  '動畫',
                  '懸疑',
                  '犯罪',
                  '驚悚',
                  '冒險',
                  '音樂',
                  '歷史',
                  '奇幻',
                  '恐怖',
                  '戰爭',
                  '傳記',
                  '歌舞',
                  '武俠',
                  '情色',
                  '災難',
                  '西部',
                  '短片',
                  '劇情',
                ] as ScreenType[]
              }
              errors={errors}
            />
          </section>
        </div>
      </section>
    </form>
  )
}

export default ScreenplayForm
