import { useContext, useMemo } from 'react'
import { useInfiniteQuery } from '@tanstack/react-query'
import { NavLink } from 'react-router-dom'
import { motion } from 'framer-motion'
import InfiniteScroll from 'react-infinite-scroll-component'
import AppContext from '../../context/AppContext'
import { ScreenplayEntry } from '../../typings'
import AuthContext from '../../context/AuthContext'
import ListRow from '../../components/List/ListRow'

const ITEMS_PER_PAGE = 5

const SubmittedScreenplaysListPage = () => {
  const { fetchScreenplayList } = useContext(AppContext)
  const { authUser } = useContext(AuthContext)

  if (!authUser) {
    return <>Loading</>
  }

  const { data, error, fetchNextPage, status, hasNextPage } = useInfiniteQuery(
    ['screenplayList'],
    ({ pageParam = 0 }) =>
      fetchScreenplayList(
        pageParam,
        ITEMS_PER_PAGE, // pageSize
        authUser.username // username
      ),
    {
      getNextPageParam: (lastPage, allPages) => {
        const currentPage = allPages.findIndex(
          (allPage) => allPage === lastPage
        )
        if (currentPage * ITEMS_PER_PAGE > lastPage.totalCount) return false
        return currentPage + 1
      },
    }
  )

  const screenplays = useMemo(
    () =>
      data?.pages.reduce(
        (a: ScreenplayEntry[] | [], c) => [...a, ...c.data],
        []
      ),
    [data]
  )

  if (status === 'loading') return <div className="py-6">Loading</div>

  if (status === 'error') return <h4>Ups!, {`${error}` as string}</h4>

  return (
    <div>
      <motion.div
        className="flex justify-between py-10 pl-6 pr-8"
        initial={{ opacity: 0 }}
        animate={{ opacity: 100 }}
        transition={{ duration: 1.25, ease: 'easeOut', delay: 0.75 }}
      >
        <h1 className="text-2xl">已投稿 ({screenplays?.length || 0})</h1>
        <div className="flex gap-x-8">
          <div>排序</div>
          <div>顯示: 所有</div>
        </div>
      </motion.div>
      <motion.figure
        className="block h-px w-full origin-left bg-black"
        initial={{ scaleX: 0 }}
        animate={{ scaleX: 1 }}
        transition={{ duration: 1.25, ease: 'easeInOut', delay: 0.5 }}
      />
      <motion.div
        className="grid grid-cols-12 py-2 pl-6 pr-8"
        initial={{ opacity: 0 }}
        animate={{ opacity: 100 }}
        transition={{ duration: 1.25, ease: 'easeOut', delay: 0.75 }}
      >
        <span className="text-small-title col-span-4">劇名</span>
        <span className="text-small-title col-span-2">類型</span>
        <span className="text-small-title col-span-4">LOGLINE</span>
        <span className="text-small-title col-span-2 text-right" />
      </motion.div>
      <motion.figure
        className="block h-px w-full origin-left bg-black"
        initial={{ scaleX: 0 }}
        animate={{ scaleX: 1 }}
        transition={{ duration: 1.25, ease: 'easeInOut', delay: 0.5 }}
      />
      <InfiniteScroll
        dataLength={screenplays ? screenplays.length : 0}
        next={() => {
          fetchNextPage()
        }}
        hasMore={!!hasNextPage}
        loader={<div className="py-4">Loading</div>}
        scrollableTarget="scrollableDiv"
      >
        <ul>
          {screenplays?.map(
            ({ id: screenplayId, name, types }: ScreenplayEntry, i) => (
              <ListRow
                key={screenplayId}
                contentTransition={{
                  duration: 1.25,
                  ease: 'easeOut',
                  delay: 1 + (i % ITEMS_PER_PAGE) * 0.25,
                }}
                lineTransition={{
                  duration: 1.25,
                  ease: 'easeInOut',
                  delay: 0.75 + (i % ITEMS_PER_PAGE) * 0.25,
                }}
              >
                <NavLink
                  to={`/screenplays/${screenplayId}/edit`}
                  className="grid cursor-pointer grid-cols-12 items-center py-12 pl-6 pr-8"
                >
                  <span className="col-span-4 text-2xl">{name}</span>
                  <span className="col-span-2">
                    {types.map((type) => type || '愛情')}
                  </span>
                  <span className="col-span-4">
                    一天，關係不錯的12歲鄰居女孩(瑪蒂達·蘭度)
                    因父親私吞了一份緝毒局黑警的毒品，全家慘...
                  </span>
                  <span className="col-span-2 text-right">
                    <button type="button" className="underline">
                      修改
                    </button>
                  </span>
                </NavLink>
              </ListRow>
            )
          )}
        </ul>
      </InfiniteScroll>
    </div>
  )
}

export default SubmittedScreenplaysListPage
