import React, { useState } from 'react'
import ReactMarkdown from 'react-markdown'
import { Screenplay } from '../../typings'
import './MarkdownEditor.css'

interface MarkdownEditorProps {
  fieldName: keyof Screenplay
  watch: any
  defaultValue?: any
  label?: string
  hintText?: string
  // FIXME
  register: any
  errors?: any
  maxLength?: number
}

const MarkdownEditor = ({
  register,
  fieldName,
  watch,
}: MarkdownEditorProps) => {
  const [showPreview, setShowPreview] = useState(false)
  const value = watch(fieldName) || ''

  return (
    <div>
      <div className="flex justify-end">
        <button
          className={`${
            !showPreview ? 'bg-[#f0f1f3]' : 'bg-[#e2e3e7]'
          } text-small-title rounded-t py-1 px-2`}
          type="button"
          onClick={() => setShowPreview(true)}
        >
          Preview
        </button>
        <button
          className={`${
            showPreview ? 'bg-[#f0f1f3]' : 'bg-[#e2e3e7]'
          } text-small-title rounded-t py-1 px-2`}
          type="button"
          onClick={() => setShowPreview(false)}
        >
          Edit style
        </button>
      </div>
      {showPreview ? (
        <div className="w-full cursor-not-allowed bg-white p-2 focus:outline-none">
          <ReactMarkdown className="markdown-preview">{value}</ReactMarkdown>
        </div>
      ) : (
        <div>
          <div className="h-8 w-full bg-[#e2e3e7]" />
          <textarea
            rows={10}
            className="w-full border border-[#f0f1f3] bg-white p-2 focus:outline-none"
            {...register(fieldName)}
            value={value}
          />
        </div>
      )}
    </div>
  )
}

export default MarkdownEditor
