import React, { useMemo, useCallback, useEffect, useState, useRef } from 'react'
import { useCookies } from 'react-cookie'
import merge from 'lodash.merge'
import { ParsedUrlQuery } from 'querystring'

export interface AuthContextProps {
  authUser:
    | {
        avatarId?: string
        id: string
        username: string
      }
    | undefined
  successMsg: string
  errorMsg: string
  authFetch: (
    input: RequestInfo,
    options?: RequestInit,
    heartbeat?: boolean
  ) => Promise<Response>
  loginFunc: (client: string, query: any) => Promise<string>
  logoutFunc: () => void
  promptSuccessMsg: (successMsg: string) => void
  promptErrorMsg: (errorMsg: string) => void
}

interface AuthState {
  successMsg: string
  errorMsg: string
}

const AuthContext = React.createContext({} as AuthContextProps)

export const DOMAIN = process.env.REACT_APP_API_DOMAIN

/*
 * AuthContextProvider to provide JWT-authenticated access to the backend
 * the backend server should be resided on the same domain as the frontend app
 *  - refresh-token is stored in the HttpOnly cookies for server authentication
 *  - JWT token is stored in client cookie
 */

// merge JWT into the fetch request header
const getJwtTokenizedOptions = (
  options: RequestInit | undefined,
  jwtToken: string | null
): RequestInit | undefined => {
  if (!jwtToken) return options

  return merge({}, options, {
    headers: {
      Authorization: `Bearer ${jwtToken}`,
    },
  })
}

export const AuthContextProvider = ({
  children,
}: {
  children: React.ReactNode
}) => {
  /* authUser in cookies
   * authUser type is
   * {
   *   id: string,
   *   username: string,
   *   roles: string[],
   *   avatarId?: string,
   * }
   */
  /* token: string in cookies */
  const [cookies, setCookies, removeCookies] = useCookies([
    'hakmaAuthUser',
    'hakmaToken',
    'hakmaRefreshToken',
  ])
  const [state, setState] = useState<AuthState>({} as AuthState)
  // useRef to avoid authFetch to reload, better approach is to divide authUser into user and jwtToken
  const tokenRef = useRef(cookies.hakmaToken)

  useEffect(() => {
    tokenRef.current = cookies.hakmaToken
  }, [cookies])

  /* define fetch function for all authorized access
   * by default, the function insert DOMAIN
   * the function overload fetch with inserting the JWT
   * if the token is expired, it will refresh and retry with the new JWT
   * if refresh is failed, user will be logged out
   */

  const authFetch = useCallback(
    (
      input: RequestInfo,
      options?: RequestInit,
      heartbeat?: boolean
    ): Promise<Response> =>
      fetch(
        `${DOMAIN}${input}`,
        getJwtTokenizedOptions(options, tokenRef.current)
      ).then((response) => {
        if (
          response.status === 401 &&
          response.headers
            .get('www-authenticate')
            ?.includes('The access token invalid or expired')
        ) {
          return fetch(`${DOMAIN}/auth/refresh-token`, {
            method: heartbeat ? 'GET' : 'POST',
            credentials: 'include',
          })
            .then((r) => {
              if (r.ok) return r.json()
              return r.text().then((errText) => {
                throw new Error(errText)
              })
            })
            .then((jwt) => {
              setCookies('hakmaToken', jwt.token, { path: '/', secure: true })
              return fetch(
                `${DOMAIN}${input}`,
                getJwtTokenizedOptions(options, jwt.token)
              )
            })
            .catch((err) => {
              // unset login token to sign out
              removeCookies('hakmaAuthUser', { path: '/', secure: true })
              removeCookies('hakmaToken', { path: '/', secure: true })
              throw err
            })
        }
        return response
      }),
    [setCookies, removeCookies]
  )

  // login funciton
  const loginFunc = useCallback(
    (client: string, query: ParsedUrlQuery) =>
      fetch(
        `${process.env.REACT_APP_API_DOMAIN}/auth/oauth?authclient=${client}&${query}`
      )
        .then((r) => {
          if (r.ok) {
            return r.json()
          }
          throw new Error(JSON.stringify(r))
        })
        .then(({ user, token, refreshToken }) => {
          if (Array.isArray(user)) {
            return 'Unknown Error'
          }
          setCookies('hakmaAuthUser', user, { path: '/', secure: true })
          setCookies('hakmaToken', token, { path: '/', secure: true })
          setCookies('hakmaRefreshToken', refreshToken, {
            path: '/',
            secure: true,
          })
          return 'OK'
        })
        .catch((error) => error.json().then((err: any) => err.message)),
    [setCookies]
  )

  // logout function
  const logoutFunc = useCallback(() => {
    fetch(`${DOMAIN}/auth/refresh-token`, { method: 'DELETE' })
    removeCookies('hakmaAuthUser', { path: '/', secure: true })
    removeCookies('hakmaToken', { path: '/', secure: true })
    removeCookies('hakmaRefreshToken', { path: '/', secure: true })
  }, [removeCookies])

  const promptSuccessMsg = useCallback(
    (successMsg: string) => {
      setState((prev) => ({
        ...prev,
        successMsg,
      }))
    },
    [setState]
  )

  const promptErrorMsg = useCallback(
    (errorMsg: string) => {
      setState((prev) => ({
        ...prev,
        errorMsg,
      }))
    },
    [setState]
  )

  const authContext = useMemo(
    () => ({
      authUser: cookies.hakmaAuthUser,
      successMsg: state.successMsg,
      errorMsg: state.errorMsg,
      authFetch,
      loginFunc,
      logoutFunc,
      promptSuccessMsg,
      promptErrorMsg,
    }),
    [cookies]
  )

  return (
    <AuthContext.Provider value={authContext}>{children}</AuthContext.Provider>
  )
}

export default AuthContext
