import { useContext, useMemo } from 'react'
import { useInfiniteQuery } from '@tanstack/react-query'
import { Link, NavLink } from 'react-router-dom'
import InfiniteScroll from 'react-infinite-scroll-component'
import AppContext from '../../context/AppContext'
import { Event } from '../../typings'
import SubHeader from '../../components/SubHeader/SubHeader'

const fetchedImgSrc = 'https://source.unsplash.com/random/?city,night'

const ITEMS_PER_PAGE = 5

const EventListPage = () => {
  const { fetchEventList } = useContext(AppContext)

  const { data, error, fetchNextPage, status, hasNextPage } = useInfiniteQuery(
    ['eventList'],
    ({ pageParam = 0 }) =>
      fetchEventList(
        pageParam,
        ITEMS_PER_PAGE // pageSize
      ),
    {
      getNextPageParam: (lastPage, allPages) => {
        const currentPage = allPages.findIndex(
          (allPage) => allPage === lastPage
        )
        if (currentPage * ITEMS_PER_PAGE > lastPage.totalCount) return false
        return currentPage + 1
      },
    }
  )

  const events = useMemo(
    () => data?.pages.reduce((a: Event[] | [], c) => [...a, ...c.data], []),
    [data]
  )

  if (status === 'loading') return <div className="py-6">Loading</div>

  if (status === 'error') return <h4>Ups!, {`${error}` as string}</h4>

  const [highlightEvent, ...restEvents] = events!

  return (
    <main>
      <SubHeader
        breadcrumbTrail={['EVENTS']}
        // secondaryAction={<>顯示: 所有</>}
      />
      <Link to={`/events/${highlightEvent.id}`}>
        <div className="grid gap-4 border-b border-graphite px-4 py-20 md:grid-cols-12">
          <div className="md:col-span-5">
            <h4 className="text-heading">{highlightEvent?.name}</h4>
            <p className="pt-6">
              每月筆尖 Challange
              都會邀請各位寫作者以三場戲為限，在我們特定的條件下創作劇本，看看誰能在限制與規則當中找到最多創作空間！
            </p>
            <span className="text-small-title pt-6">截止日期： 2023.10.02</span>
            <NavLink to="" className="text-small-title block pt-6">
              了解更多
            </NavLink>
          </div>
          <div className="order-first md:order-last	md:col-span-7">
            <div
              style={{ backgroundImage: `url(${fetchedImgSrc})` }}
              className="aspect-video w-full"
            />
          </div>
        </div>
      </Link>
      <InfiniteScroll
        dataLength={events ? events.length : 0}
        next={() => {
          fetchNextPage()
        }}
        hasMore={!!hasNextPage}
        loader={<div className="py-4">Loading</div>}
        scrollableTarget="scrollableDiv"
      >
        <div className="grid md:grid-cols-2 lg:grid-cols-3">
          {restEvents?.map(({ id, name }) => (
            <Link key={id} to={`/events/${id}`}>
              <div className="col-span-1 border-b border-r border-graphite p-4">
                <div
                  style={{ backgroundImage: `url(${fetchedImgSrc})` }}
                  className="aspect-video w-full"
                />

                <h4 className="pt-4 text-2xl">{name}</h4>
                <span className="text-small-title pt-4">
                  截止日期： 2023.10.02
                </span>
                <NavLink to="" className="text-small-title block pt-4">
                  了解更多
                </NavLink>
              </div>
            </Link>
          ))}
        </div>
      </InfiniteScroll>
    </main>
  )
}

export default EventListPage
