import { useContext, useEffect, useState } from 'react'
import { Screenplay } from '../../typings'
import ScreenplayForm from '../../components/form/Screenplay'
import AuthContext from '../../context/AuthContext'

const NewScreenplayPage = () => {
  const { authUser } = useContext(AuthContext)
  const [screenplay, setScreenplay] = useState<null | Screenplay>(null)

  useEffect(() => {
    setScreenplay({
      usernames: [authUser?.username].filter((v) => v) as string[],
      name: '',
      outline: '',
      logline: '',
      videoUrl: '',
      isDeleted: 0,
      script: [
        {
          time: '',
          scene: '',
          content: '',
          session: '',
        },
      ],
      characters: [
        {
          role: '',
          actor: '',
          description: '',
        },
      ],
      confession: '',
      types: [],
      languages: [],
      fileId: '',
      likes: 0,
      isLiked: false,
      createdAt: 0,
      updatedAt: 0,
    })
  }, [authUser?.username])

  if (screenplay === null) {
    return <>Loading</>
  }

  return <ScreenplayForm screenplay={screenplay} />
}

export default NewScreenplayPage
