import React, { useContext, useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import AppContext from '../../context/AppContext'
import AuthContext from '../../context/AuthContext'
import { User } from '../../typings'
import Facebook from '../../assets/icons/Facebook'
import Instagram from '../../assets/icons/Instagram'
import YouTube from '../../assets/icons/Youtube'
import ScreenplayList from '../../components/Screenplay/ScreenplayList'
import SubHeader from '../../components/SubHeader/SubHeader'
import Avatar from '../../components/Screenwriter/Avatar'

const ScreenwriterPage = () => {
  const { screenwriterId } = useParams()
  const { fetchProfile } = useContext(AppContext)
  const { authUser } = useContext(AuthContext)
  const [screenwriter, setScreenwriter] = useState<null | User>(null)

  useEffect(() => {
    fetchProfile(screenwriterId ?? '').then((v) => {
      setScreenwriter(v)
    })
  }, [fetchProfile, setScreenwriter, screenwriterId])

  if (!screenwriter) {
    return <>Loading...</>
  }

  return (
    <main>
      <SubHeader breadcrumbTrail={['SCREENWRITER_DETAIL']} />
      <section className="grid grid-cols-12 px-4 py-10 md:py-16 xl:py-20">
        <div className="col-span-12 max-w-screen-lg md:col-span-9 lg:col-span-8">
          <section className="flex items-center gap-4">
            <Avatar
              avatarId={screenwriter.avatarId}
              name={screenwriter.displayName}
            />
            <h1 className="text-heading">{screenwriter.displayName}</h1>
          </section>
          <p className="mt-11">{screenwriter.intro}</p>
          <section className="mt-10 rounded-md bg-grey2 p-4">
            <div>
              <h5 className="text-graphite">技能</h5>
              <div className="flex gap-2">
                {screenwriter.skills?.map((skill) => (
                  <span key={skill}>{skill}</span>
                ))}
              </div>
            </div>
            <div className="mt-6">
              <h5 className="text-graphite">擅長作品類型</h5>
              <div className="flex gap-2">
                {screenwriter.types?.map((type) => (
                  <span key={type}>{type}</span>
                ))}
              </div>
            </div>
            <div className="mt-6">
              <h5 className="text-graphite">媒介</h5>
              <div className="flex gap-2">
                {screenwriter.medium?.map((medium) => (
                  <span key={medium}>{medium}</span>
                ))}
              </div>
            </div>
            <div className="mt-6">
              <h5 className="text-graphite">聯絡我</h5>
              <div>{screenwriter.email}</div>
            </div>
            <div className="mt-4 flex gap-4">
              {screenwriter.facebook && (
                <a
                  href={screenwriter.facebook}
                  target="_blank"
                  rel="noreferrer"
                >
                  <Facebook className="h-4 w-4 fill-black" />
                </a>
              )}
              {screenwriter.instagram && (
                <a
                  href={screenwriter.instagram}
                  target="_blank"
                  rel="noreferrer"
                >
                  <Instagram className="h-4 w-4 fill-black" />
                </a>
              )}
              {screenwriter.youtube && (
                <a href={screenwriter.youtube} target="_blank" rel="noreferrer">
                  <YouTube className="h-4 w-4 fill-black" />
                </a>
              )}
            </div>
          </section>
          {authUser?.id === screenwriterId && (
            <Link className="mt-6 block" to="/profile">
              更改個人資料
            </Link>
          )}
        </div>
      </section>
      <section>
        <div className="text-heading border-t border-graphite p-4">
          以往作品連結
        </div>
        <ScreenplayList itemsPerPage={5} username={authUser?.username} />
      </section>
    </main>
  )
}

export default ScreenwriterPage
