import React, { useContext, useEffect, useState } from 'react'
import AuthContext from '../context/AuthContext'
import AppContext from '../context/AppContext'
import { User } from '../typings'
import ModalContext from '../context/ModalContext'
import PersonalInfoModalContent from '../components/Modal/PersonalInfoModalContent'
import SocialNetworkModalContent from '../components/Modal/SocialNetworkModalContent'
import SubHeader from '../components/SubHeader/SubHeader'
import Avatar from '../components/Screenwriter/Avatar'

const ProfilePage = () => {
  const { fetchProfile } = useContext(AppContext)
  const { authUser } = useContext(AuthContext)
  const [profile, setProfile] = useState<null | User>(null)
  const { handleModal } = useContext(ModalContext)

  if (!authUser) {
    return <>Loading...</>
  }

  useEffect(() => {
    fetchProfile(authUser.id).then((v) => {
      setProfile(v)
    })
  }, [fetchProfile, setProfile, authUser.id])

  if (!profile) {
    return <>Loading...</>
  }

  return (
    <>
      <SubHeader breadcrumbTrail={['PROFILE']} />
      <main className="px-4 py-10">
        <div className="grid grid-cols-12">
          <div className="col-span-12 max-w-screen-lg md:col-span-9 lg:col-span-8">
            <h1 className="text-heading">設定</h1>
            <section className="mt-20">
              <div className="w-full border-b border-graphite">
                <button type="button" className="border-b-2 py-2">
                  基本資料
                </button>
              </div>
              <div className="flex w-full items-center justify-between py-6">
                <div>Email address</div>
                <div className="text-graphite">{profile.contactEmail}</div>
              </div>
              <div className="flex w-full items-center justify-between py-6">
                <div>
                  個人資料
                  <p className="mt-2 text-graphite">
                    更改你的大頭照，簡短個人介紹，技能，擅長作品類型，媒介
                  </p>
                </div>
                <div className="flex items-center gap-2 text-graphite">
                  <Avatar
                    avatarId={profile.avatarId}
                    name={profile.displayName}
                  />
                  <span
                    className="cursor-pointer"
                    onClick={() =>
                      handleModal(
                        <PersonalInfoModalContent profile={profile} />
                      )
                    }
                  >
                    {profile.displayName}
                  </span>
                </div>
              </div>
              <div className="flex w-full items-center justify-between py-6">
                <div>
                  Social network
                  <p className="mt-2 text-graphite">更改你的聯絡方法</p>
                </div>
                <div
                  className="cursor-pointer text-graphite"
                  onClick={() =>
                    handleModal(<SocialNetworkModalContent profile={profile} />)
                  }
                >
                  更改
                </div>
              </div>
            </section>
          </div>
        </div>
      </main>
    </>
  )
}

export default ProfilePage
