import { createBrowserRouter, RouterProvider } from 'react-router-dom'
import { RoutePaths } from './enums/routeEnum'
import Root from './pages/Root'
import ErrorPage from './pages/ErrorPage'
import Login from './pages/Login'
import ScriptListPage from './pages/Screenplay/ListPage'
import EditScreenplayPage from './pages/Screenplay/EditScreenplayPage'
import NewScreenplayPage from './pages/Screenplay/NewScreenplayPage'
import SubmittedListPage from './pages/Screenplay/SubmittedListPage'
import ScreenplayPage from './pages/Screenplay/ScreenplayPage'
import ProfilePage from './pages/ProfilePage'
import AboutPage from './pages/AboutPage'
import EventListPage from './pages/Event/ListPage'
import EventPage from './pages/Event/EventPage'
import HelpPage from './pages/HelpPage'
import ScreenwriterListPage from './pages/Screenwriter/ListPage'
import ScreenwriterPage from './pages/Screenwriter/ScreenwriterPage'
import DisclaimerPage from './pages/DisclaimerPage'
import ContactUsPage from './pages/ContactUsPage'
import FundingPage from './pages/FundingPage'

const router = createBrowserRouter([
  {
    path: '/',
    element: <Root />,
    errorElement: <ErrorPage />,
    children: [
      { index: true, element: <ScriptListPage /> },
      {
        path: RoutePaths.SCREENPLAY_LIST,
        element: <ScriptListPage />,
      },
      {
        path: RoutePaths.SCREENPLAY_DETAIL,
        element: <ScreenplayPage />,
      },
      {
        path: RoutePaths.SCREENPLAY_EDIT,
        element: <EditScreenplayPage />,
      },
      {
        path: RoutePaths.SCREENPLAY_NEW,
        element: <NewScreenplayPage />,
      },
      {
        path: RoutePaths.SCREENPLAY_SUBMITTED,
        element: <SubmittedListPage />,
      },
      {
        path: RoutePaths.PROFILE,
        element: <ProfilePage />,
      },
      {
        path: RoutePaths.ABOUT,
        element: <AboutPage />,
      },
      {
        path: RoutePaths.EVENTS,
        element: <EventListPage />,
      },
      {
        path: RoutePaths.EVENT_DETAIL,
        element: <EventPage />,
      },
      {
        path: RoutePaths.HELP,
        element: <HelpPage />,
      },
      {
        path: RoutePaths.SCREENWRITER_LIST,
        element: <ScreenwriterListPage />,
      },
      {
        path: RoutePaths.SCREENWRITER_DETAIL,
        element: <ScreenwriterPage />,
      },
      {
        path: RoutePaths.DISCLAIMER,
        element: <DisclaimerPage />,
      },
      {
        path: RoutePaths.CONTACT_US,
        element: <ContactUsPage />,
      },
      {
        path: RoutePaths.FUNDING,
        element: <FundingPage />,
      },
    ],
  },
  {
    path: RoutePaths.LOGIN,
    element: <Login />,
  },
])

const App = () => <RouterProvider router={router} />

export default App
