import React, { useContext, useEffect, useRef, useState } from 'react'
import { motion } from 'framer-motion'
import { useNavigate } from 'react-router'
import { Link } from 'react-router-dom'
import Expand from '../../assets/icons/Expand'
import Logout from '../../assets/icons/Logout'
import Profile from '../../assets/icons/Profile'
import Write from '../../assets/icons/Write'
import AuthContext from '../../context/AuthContext'
import ModalContext from '../../context/ModalContext'
import LoginModalContent from '../Modal/LoginModalContent'
import Avatar from '../Screenwriter/Avatar'

const UserDropdown = () => {
  const { authUser, logoutFunc } = useContext(AuthContext)
  const { handleModal } = useContext(ModalContext)
  const [isOpen, setIsOpen] = useState(false)
  const dropdownRef = useRef<HTMLUListElement>(null)

  useEffect(() => {
    function handleClickOutside(event: MouseEvent) {
      if (
        dropdownRef.current &&
        !dropdownRef.current.contains(event.target as Node)
      ) {
        setIsOpen(false)
      }
    }

    document.addEventListener('mousedown', handleClickOutside)
    return () => {
      document.removeEventListener('mousedown', handleClickOutside)
    }
  }, [dropdownRef])

  const navigate = useNavigate()

  const isComponentAnimationLoaded =
    sessionStorage.getItem('componentAnimation') === 'loaded'

  if (authUser) {
    return (
      <div className="relative">
        <div
          className="inline-flex cursor-pointer items-center gap-2 bg-stroke04 bg-contain bg-center bg-no-repeat p-0.5 pl-6"
          onClick={() => setIsOpen(!isOpen)}
        >
          <Avatar
            avatarId={authUser.avatarId!}
            name={authUser.username}
            size="medium"
          />
          <div className="cursor-pointer">
            <Expand className="h-4 w-4 fill-black" />
          </div>
        </div>
        {isOpen && (
          <ul
            className="text-small-title absolute left-6 top-12 flex flex-col whitespace-nowrap border border-graphite bg-white md:left-auto md:right-0"
            ref={dropdownRef}
            onClick={() => setIsOpen(false)}
          >
            <li className="cursor-pointer pl-3 pr-6 pt-4 pb-3">
              <Link className="flex items-center gap-4" to="/profile">
                <Profile className="h-4 w-4 fill-black" />
                個人資料
              </Link>
            </li>
            <li className="cursor-pointer py-2 pl-3 pr-6">
              <Link className="flex items-center gap-4" to="/screenplays/new">
                <Write className="h-4 w-4 fill-black" />
                投稿
              </Link>
            </li>
            <li
              className="cursor-pointer border-t border-graphite pl-3 pr-6 pt-3 pb-4"
              onClick={() => {
                logoutFunc()
                navigate('/')
              }}
            >
              <div className="flex items-center gap-4">
                <Logout className="h-4 w-4 fill-black" />
                登出
              </div>
            </li>
          </ul>
        )}
      </div>
    )
  }

  return (
    <motion.div
      initial={isComponentAnimationLoaded ? false : { opacity: 0, y: '100%' }}
      animate={{ opacity: 1, y: 0 }}
      transition={{
        duration: 0.5,
        delay: 1.1 + 0.25 * (3 + 1), // FIXME 3 is harcoded
        ease: 'easeOut',
      }}
      className="text-heading inline-block cursor-pointer bg-contain bg-center bg-no-repeat pl-6 pr-8 pt-6 pb-4 hover:bg-stroke01 md:pr-6 md:pt-3 md:pb-1 "
      onClick={() => handleModal(<LoginModalContent />)}
    >
      <span className="text-heading md:hidden">登入</span>
      <span className="text-small-title hidden md:block">登入</span>
    </motion.div>
  )
}

export default UserDropdown
