import { useContext } from 'react'
import ModalContext from '../context/ModalContext'
import Close from '../assets/icons/close.svg'

const Modal = () => {
  const { modalContent, handleModal, isOpen } = useContext(ModalContext)

  return (
    <div
      className={`fixed inset-0 z-10 h-screen w-screen overflow-hidden ${
        !isOpen && 'hidden'
      }`}
      onClick={() => handleModal(false)}
    >
      <dialog
        open
        className="fixed inset-0 flex h-full w-full flex-col overflow-y-auto border border-black bg-white p-10 md:mt-20 md:h-[calc(100%-80px)] md:max-w-[550px]"
        onClick={(e) => e.stopPropagation()}
      >
        {' '}
        <button
          type="button"
          className="translate-y-full cursor-pointer self-end"
          onClick={() => handleModal(false)}
        >
          <img src={Close} alt="關閉" />
        </button>
        <div>{modalContent}</div>
      </dialog>
    </div>
  )
}

export default Modal
