import { useState } from 'react'

interface AvatarProps {
  avatarId: string | null
  name: string
  size?: 'large' | 'medium' | 'small'
  overrideUrl?: string | null
}

const sizeClassMapping = {
  large: 'h-12 w-12',
  medium: 'h-8 w-8',
  small: 'h-4 w-4',
}

const Avatar = ({
  avatarId,
  name,
  size = 'large',
  overrideUrl,
}: AvatarProps) => {
  const [showAlternative, setShowAlternative] = useState(false)

  return (
    <div>
      {showAlternative ? (
        <div
          className={`${sizeClassMapping[size]} flex items-center justify-center rounded-full border border-graphite`}
        >
          {name.substring(0, 1).toUpperCase()}
        </div>
      ) : (
        <img
          alt="大頭照"
          className={`${sizeClassMapping[size]} rounded-full`}
          src={
            overrideUrl || `${process.env.REACT_APP_API_DOMAIN}/a/${avatarId}`
          }
          onError={() => setShowAlternative(true)}
        />
      )}
    </div>
  )
}

export default Avatar
