import React, { useContext, useCallback, useMemo } from 'react'
import AuthContext from './AuthContext'
import { Event, FileType, Screenplay, ScreenplayEntry, User } from '../typings'

export interface AppContextProps {
  fetchScreenplayList: (
    page: number,
    pageSize: number,
    usename?: string,
    type?: string
  ) => Promise<{ data: ScreenplayEntry[]; totalCount: number }>
  fetchScreenplay: (id: string) => Promise<Screenplay>
  saveScreenplay: (screenplay: Screenplay) => Promise<string> // return screenplay ID
  uploadFile: (data: FormData) => Promise<FileType>
  fetchYearScreenplays: (
    year: number
  ) => Promise<{ data: ScreenplayEntry[]; totalCount: number }>
  getVotes: () => Promise<{ screenplayIds: string[] }> // return vote dictionary of the logged in user
  upVote: (id: string) => Promise<'success' | 'failed'>
  downVote: (id: string) => Promise<'success' | 'failed'>
  // event
  fetchEventList: (
    page: number,
    pageSize: number
  ) => Promise<{ data: Event[]; totalCount: number }>
  fetchEvent: (id: number) => Promise<Event>
  fetchProfile: (id: string) => Promise<User>
  updateProfile: (user: User) => Promise<string>
  uploadAvatar: (data: FormData) => Promise<FileType>
  fetchScreenwriterList: (
    page: number,
    pageSize: number
  ) => Promise<{ data: User[]; totalCount: number }>
}

const AppContext = React.createContext({} as AppContextProps)

export const AppContextProvider = ({
  children,
}: {
  children: React.ReactNode
}) => {
  const { authFetch } = useContext(AuthContext)

  const fetchScreenplayList = useCallback(
    (page: number, pageSize: number, username?: string, type?: string) =>
      authFetch(
        `/screenplay/list?${new URLSearchParams({
          page: `${page}`,
          pageSize: `${pageSize}`,
          username: username ?? '',
          type: type || '',
        })}`
      ).then((r) => r.json()),
    [authFetch]
  )

  const fetchScreenplay = useCallback(
    (id: string) =>
      authFetch(`/screenplay/read?${new URLSearchParams({ id })}`).then((r) =>
        r.json()
      ),
    [authFetch]
  )

  const saveScreenplay = useCallback(
    (screenplay: Screenplay) =>
      authFetch('/screenplay/upsert', {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(screenplay),
      }).then((r) => r.json()),
    [authFetch]
  )

  const uploadFile = useCallback(
    (data: FormData) => {
      data.append('type', 'screenplay')
      return authFetch('/file/upload', {
        method: 'POST',
        body: data,
      }).then((r) => r.json())
    },
    [authFetch]
  )

  // TODO: Support Images extension
  const uploadAvatar = useCallback(
    (data: FormData) => {
      data.append('type', 'avatar')
      return authFetch('/file/upload', {
        method: 'POST',
        body: data,
      }).then((r) => r.json())
    },
    [authFetch]
  )

  // TODO: const getFile(fileId: id)
  // https://hakma-api.organizers.hk/a/<avatarId>
  // e.g., https://hakma-api.organizers.hk/a/a1d4c179-06bc-4220-995f-bc17846d65c6

  const upVote = useCallback(
    (id: string) =>
      authFetch('/screenplay/up-vote', {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(id),
      }).then((r) => r.json()),
    [authFetch]
  )

  const downVote = useCallback(
    (id: string) =>
      authFetch('/screenplay/down-vote', {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(id),
      }).then((r) => r.json()),
    [authFetch]
  )

  const fetchYearScreenplays = useCallback(
    (year: number) =>
      authFetch(
        `/screenplay/list-year?${new URLSearchParams({
          year: `${year}`,
        })}`
      ).then((r) => r.json()),
    [authFetch]
  )

  const getVotes = useCallback(
    () => authFetch('/screenplay/get-votes').then((r) => r.json()),
    [authFetch]
  )

  const fetchEventList = useCallback(
    (page: number, pageSize: number) =>
      authFetch(
        `/event/list?${new URLSearchParams({
          page: `${page}`,
          pageSize: `${pageSize}`,
        })}`
      ).then((r) => r.json()),
    [authFetch]
  )

  const fetchEvent = useCallback(
    (id: number) =>
      authFetch(`/event/get?${new URLSearchParams({ id: `${id}` })}`).then(
        (r) => r.json()
      ),
    [authFetch]
  )

  const fetchProfile = useCallback(
    (id: string) =>
      authFetch(`/profile/get?${new URLSearchParams({ id: `${id}` })}`).then(
        (r) => r.json()
      ),
    [authFetch]
  )

  const updateProfile = useCallback(
    (user: User) =>
      authFetch(`/profile/update`, {
        headers: {
          'Content-Type': 'application/json',
        },
        method: 'POST',
        body: JSON.stringify(user),
      }).then((r) => r.json()),
    [authFetch]
  )

  const fetchScreenwriterList = useCallback(
    (page: number, pageSize: number) =>
      authFetch(
        `/screenwriter/list?${new URLSearchParams({
          page: `${page}`,
          pageSize: `${pageSize}`,
        })}`
      ).then((r) => r.json()),
    [authFetch]
  )
  return useMemo(
    () => (
      <AppContext.Provider
        value={{
          fetchScreenplayList,
          fetchScreenplay,
          saveScreenplay,
          uploadFile,
          fetchYearScreenplays,
          upVote,
          downVote,
          getVotes,
          fetchEventList,
          fetchEvent,
          fetchProfile,
          updateProfile,
          uploadAvatar,
          fetchScreenwriterList,
        }}
      >
        {children}
      </AppContext.Provider>
    ),
    []
  )
}

export default AppContext
