export interface TextAreaProps {
  fieldName: any
  watch: any
  register: any
  label?: string
  hintText?: string
  // FIXME
  defaultValue?: any
  errors?: any
  maxLength?: number
}

export const TextArea = ({
  fieldName,
  watch,
  register,
  defaultValue,
  label,
  hintText,
  errors,
  maxLength,
}: TextAreaProps) => {
  const value = watch(fieldName) || ''
  return (
    <div>
      {label && (
        <label className="block text-graphite" htmlFor={fieldName}>
          {label}
        </label>
      )}
      <textarea
        rows={Math.ceil((value as string).length / 50) || 1}
        placeholder={defaultValue}
        className="w-full border-b border-graphite bg-transparent py-2 focus:outline-none"
        {...register(fieldName, {
          maxLength,
        })}
      />
      <div className="text-small-title flex justify-between justify-items-end text-graphite">
        <span>{hintText || ''}</span>
        {maxLength && (
          <span className="">
            {(value as string).length || 0}
            <span className="text-graphite">/{maxLength}</span>
          </span>
        )}
      </div>
      {errors[fieldName]?.type === 'maxLength' && (
        <div className="text-small-title mt-2 w-full bg-[#fdefe5] p-1 text-[#F05F00]">
          請最多輸入{maxLength}字
        </div>
      )}
    </div>
  )
}
