interface PaginateProps {
  currentPage: number
  totalCount: number
  paginate: (pageNumber: number) => void
  isPreviousData: boolean
  itemsPerPage: number
}

const Paginate = ({
  currentPage,
  totalCount,
  paginate,
  isPreviousData,
  itemsPerPage,
}: PaginateProps) => {
  const totalNoOfPages = Math.ceil(totalCount / itemsPerPage)
  let pageNumbers = []

  for (let i = currentPage - 2; i <= currentPage + 2; i += 1) {
    pageNumbers.push(i)
  }

  pageNumbers = pageNumbers.filter(
    (pageNumber) => pageNumber >= 1 && pageNumber <= totalNoOfPages
  )

  const hasMore = currentPage < totalNoOfPages

  if (pageNumbers.length <= 1) {
    return null
  }

  return (
    <div className="text-small-title flex w-full justify-center gap-8 py-3">
      {currentPage > 1 && (
        <button
          type="button"
          onClick={() => paginate(currentPage - 1)}
          disabled={currentPage === 1}
        >
          上頁
        </button>
      )}
      <ul className="flex md:gap-4">
        {pageNumbers.map((pageNumber: number) => (
          <li
            key={pageNumber}
            onClick={() => paginate(pageNumber)}
            className={`${
              currentPage === pageNumber &&
              'bg-stroke04 bg-contain bg-center bg-no-repeat'
            } cursor-pointer p-3`}
          >
            {pageNumber}
          </li>
        ))}
      </ul>
      {currentPage < totalNoOfPages && (
        <button
          type="button"
          onClick={() => {
            if (!isPreviousData && hasMore) {
              paginate(currentPage + 1)
            }
          }}
          // Disable the Next Page button until we know a next page is available
          disabled={isPreviousData || !hasMore}
        >
          下頁
        </button>
      )}
      {currentPage < totalNoOfPages && (
        <button type="button" onClick={() => paginate(totalNoOfPages)}>
          最後
        </button>
      )}
    </div>
  )
}

export default Paginate
