import { useContext, useState } from 'react'
import {
  useForm,
  SubmitHandler,
  UseFormWatch,
  UseFormRegister,
  Controller,
} from 'react-hook-form'
import { Medium, User as Profile, ScreenType, Skill } from '../../typings'
import AppContext from '../../context/AppContext'
import { TextArea, TextAreaProps } from './TextArea'
import ModalContext from '../../context/ModalContext'
import { ChipGroup } from './ChipGroup'
import Stroke03 from '../../assets/strokes/Stroke03'
import Avatar from '../Screenwriter/Avatar'

interface PersonalInfoFormProps extends Profile {
  files?: FileList
}
interface PersonalInfoTextAreaProps extends TextAreaProps {
  fieldName: keyof Profile
  watch: UseFormWatch<Profile>
  register: UseFormRegister<Profile>
}
interface ProfileFormProps {
  profile: Profile
}

const PersonalInfoTextArea = ({
  fieldName,
  watch,
  register,
  label,
  hintText,
  errors,
  maxLength,
}: PersonalInfoTextAreaProps) => (
  <TextArea
    fieldName={fieldName}
    label={label}
    hintText={hintText}
    register={register}
    watch={watch}
    errors={errors}
    maxLength={maxLength}
  />
)

const PersonalInfoForm = ({ profile }: ProfileFormProps) => {
  const { updateProfile, uploadAvatar } = useContext(AppContext)
  const { handleModal } = useContext(ModalContext)
  const [imageUrl, setImageUrl] = useState<string | null>(null)

  const {
    register,
    control,
    formState: { errors },
    handleSubmit,
    watch,
  } = useForm<PersonalInfoFormProps>({
    mode: 'onChange',
    defaultValues: profile,
  })

  const onSubmit: SubmitHandler<PersonalInfoFormProps> = (data) => {
    const postData = () => {
      const updatedPersonalInfoData = {
        ...profile,
        ...data,
      }

      updateProfile(updatedPersonalInfoData).then((profileId) => {
        if (profileId) {
          handleModal(false)
        }
      })
    }

    // Upload Avatar
    if (data.files?.length) {
      const formData = new FormData()
      formData.append('file', data.files[0])
      formData.append('name', data.files[0].name)
      uploadAvatar(formData).then((file) => {
        if (file) {
          data.avatarId = file.id
          postData()
        }
      })
    } else {
      postData()
    }
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <section className="flex flex-col gap-10">
        <Controller
          name="files"
          control={control}
          render={({ field }) => (
            <div className="">
              <label className="mb-2 block text-graphite">大頭照</label>
              <div className="flex gap-x-4">
                <Avatar
                  avatarId={profile.avatarId}
                  name={profile.displayName}
                  overrideUrl={imageUrl}
                />
                <div className="text-small-title">
                  <input
                    accept="image/*"
                    id="file-input"
                    hidden
                    type="file"
                    onChange={(e) => {
                      field.onChange(e.target.files)
                      const file = e.target.files && e.target.files[0]
                      if (file) {
                        const newImageUrl = URL.createObjectURL(file)
                        setImageUrl(newImageUrl)
                      }
                    }}
                  />
                  <label
                    htmlFor="file-input"
                    className="cursor-pointer text-green"
                  >
                    更新
                  </label>
                  <button
                    type="button"
                    className="ml-4 text-orange"
                    onClick={() => setImageUrl(null)}
                  >
                    除去
                  </button>
                  <p className="md:max-w-[75%]">
                    JPG/PNG檔案，10MB以下，正方形大頭為佳，Resolution: 300px
                  </p>
                </div>
              </div>
            </div>
          )}
        />
        <PersonalInfoTextArea
          fieldName="displayName"
          label="名字"
          hintText="其他人看你的名字"
          register={register}
          watch={watch}
          errors={errors}
          maxLength={50}
        />

        <PersonalInfoTextArea
          fieldName="intro"
          label="個人介紹"
          hintText="其他人看到你的簡介"
          register={register}
          watch={watch}
          errors={errors}
          maxLength={150}
        />

        <ChipGroup
          label="技能 +"
          watch={watch}
          fieldName="skills"
          register={register}
          errors={errors}
          options={
            [
              '編劇',
              '攝影',
              '策劃',
              '美術',
              '剪接',
              '音效',
              '動作設計',
            ] as Skill[]
          }
        />

        <ChipGroup
          label="擅長作品類型 +"
          watch={watch}
          fieldName="types"
          register={register}
          errors={errors}
          options={
            [
              '喜劇',
              '愛情',
              '動作',
              '科幻',
              '動畫',
              '懸疑',
              '犯罪',
              '驚悚',
              '冒險',
              '音樂',
              '歷史',
              '奇幻',
              '恐怖',
              '戰爭',
              '傳記',
              '歌舞',
              '武俠',
              '情色',
              '災難',
              '西部',
              '短片',
              '劇情',
            ] as ScreenType[]
          }
        />

        <ChipGroup
          label="媒介 +"
          watch={watch}
          fieldName="medium"
          register={register}
          errors={errors}
          options={
            [
              '電影',
              '電視劇',
              '短片',
              '視覺藝術',
              '多媒體',
              '戲劇',
              'MTV',
              '動畫',
              '廣告',
            ] as Medium[]
          }
        />
        <div className="text-heading flex cursor-pointer items-center gap-x-1 bg-highlight02 bg-contain bg-no-repeat">
          <Stroke03 className="h-8 xl:h-10" />
          <input type="submit" value="儲存" />
        </div>
      </section>
    </form>
  )
}

export default PersonalInfoForm
