import { AnimatePresence, motion } from 'framer-motion'
import Plus from '../plus.svg'
import Minus from '../minus.svg'

interface AccordionProps {
  title: string
  content: React.ReactNode
  open: boolean
  onChange: () => void
}

const Accordion = ({ title, content, open, onChange }: AccordionProps) => (
  <motion.li
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    transition={{ delay: 1, duration: 1.25, ease: 'easeOut' }}
    onClick={onChange}
  >
    <section
      className={`text-title flex cursor-pointer place-content-between bg-white p-6 ${
        !open && 'border-b border-black'
      }`}
    >
      {title}
      <figure className="flex place-content-between">
        {open ? <img src={Minus} alt="關閉" /> : <img src={Plus} alt="展開" />}
      </figure>
    </section>
    <AnimatePresence initial={false}>
      {open && (
        <motion.div
          initial="collapsed"
          animate="open"
          exit="collapsed"
          variants={{
            open: { opacity: 1, height: 'auto' },
            collapsed: { opacity: 0, height: 0 },
          }}
          transition={{ duration: 1, ease: 'easeOut' }}
        >
          <div className="border-b border-black bg-white p-6">{content}</div>
        </motion.div>
      )}
    </AnimatePresence>
  </motion.li>
)

export default Accordion
