import { useContext, useEffect, useState } from 'react'
import { Link, useParams } from 'react-router-dom'
import { format } from 'date-fns'
import { motion } from 'framer-motion'
import ReactMarkdown from 'react-markdown'
import AppContext from '../../context/AppContext'
import { Screenplay } from '../../typings'
import Accordion from '../../widgets/Accordion'
import Favorite from '../../favorite.svg'
import Favorited from '../../favorited.svg'
import Stroke03 from '../../assets/strokes/Stroke03'
import SubHeader from '../../components/SubHeader/SubHeader'
import Avatar from '../../components/Screenwriter/Avatar'

type ScreenplayAccordion = 'script' | 'confession'

export const parseYouTubeURL = (url: string): string => {
  const regExp =
    /^.*((youtu.be\/)|(v\/)|(\/u\/\w\/)|(embed\/)|(watch\?))\??v?=?([^#&?]*).*/
  const match = url.match(regExp)
  return match && match[7].length === 11 ? match[7] : ''
}

interface YoutubeVideoProps {
  url: string
}

export const YoutubeVideo = ({ url }: YoutubeVideoProps) => (
  <div className="relative aspect-video w-full">
    <iframe
      title="Youtube Video"
      className="absolute top-0 left-0 h-full w-full"
      src={`https://www.youtube.com/embed/${parseYouTubeURL(url)}`}
      allowFullScreen
    />
  </div>
)

const ScreenplayPage = () => {
  const { screenplayId } = useParams()
  const { fetchScreenplay, upVote, downVote } = useContext(AppContext)
  const [screenplay, setScreenplay] = useState<null | Screenplay>(null)
  const [panel, setPanel] = useState<ScreenplayAccordion>('script')

  useEffect(() => {
    fetchScreenplay(screenplayId ?? '').then((v) => {
      setScreenplay(v)
    })
  }, [fetchScreenplay, setScreenplay, screenplayId])

  if (screenplay === null) {
    return <>Loading...</>
  }

  return (
    <>
      <SubHeader
        breadcrumbTrail={['SCREENPLAY_DETAIL']}
        secondaryAction={
          <div className="flex gap-x-8">
            {/* TODO: Instant feedback */}
            <button
              type="button"
              className="text-x flex items-center gap-2"
              onClick={() => {
                if (screenplay.isLiked) {
                  downVote(screenplayId!)
                } else {
                  upVote(screenplayId!)
                }
              }}
            >
              {screenplay.isLiked ? (
                <img src={Favorited} alt="已點讚" />
              ) : (
                <img src={Favorite} alt="點讚" />
              )}{' '}
              {screenplay.likes}
            </button>
          </div>
        }
      />
      <section className="flex flex-col justify-between border-b border-graphite p-4 md:flex-row md:items-center">
        <motion.h1
          className="text-heading"
          initial={{ opacity: 0, y: '50%' }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ delay: 1, duration: 1.25, ease: 'easeOut' }}
        >
          {screenplay.name}
        </motion.h1>
        <Link
          className="text-heading mt-4 flex items-center gap-x-1 bg-highlight01 bg-contain bg-no-repeat md:mt-0"
          to={`/screenwriters/${
            Array.isArray(screenplay.screenwriters) &&
            screenplay.screenwriters[0]?.id
          }`}
        >
          <Stroke03 className="h-8 xl:h-10" />
          想請佢
        </Link>
      </section>
      <main className="overflow-y-hidden md:grid md:h-screen md:grid-cols-[1fr_1px_1fr]">
        <section className="max-h-screen overflow-y-auto py-4 px-6">
          <motion.div
            className=""
            initial={{ opacity: 0, y: '10%' }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 1.25, duration: 1.25, ease: 'easeOut' }}
          >
            {screenplay.videoUrl && (
              <div className="mb-10">
                <YoutubeVideo url={screenplay.videoUrl} />
              </div>
            )}
            <h6 className="text-graphite">故事大綱</h6>
            <p>{screenplay.outline}</p>
          </motion.div>
          <motion.div
            className="mt-10 grid grid-cols-[repeat(auto-fill,_minmax(160px,_1fr))] gap-6"
            initial={{ opacity: 0, y: '50%' }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ delay: 1.25, duration: 1.25, ease: 'easeOut' }}
          >
            <div>
              <h6 className="text-graphite">類型</h6>
              <div>{screenplay.types.map((tag) => `${tag}`).join(', ')}</div>
            </div>
            <div>
              <h6 className="text-graphite">編劇</h6>
              <div>
                {screenplay.screenwriters
                  ?.map(({ username }) => `${username}`)
                  .join(', ')}
              </div>
            </div>
            <div>
              <h6 className="text-graphite">上傳日期</h6>
              <div>
                {format(Number(screenplay.createdAt) * 1000, 'yyyy-MM-dd')}
              </div>
            </div>
            <div>
              <h6 className="text-graphite">人物簡介</h6>
              <div>
                {screenplay.characters.map((character) => (
                  <div key={character.actor}>
                    <span>{`${character.actor} ${character.role}`}</span>
                    <p>{character.description}</p>
                  </div>
                ))}
              </div>
            </div>
          </motion.div>
        </section>
        <motion.figure
          className="block w-px origin-top bg-black md:h-screen"
          initial={{ scaleY: 0 }}
          animate={{ scaleY: 1 }}
          transition={{ duration: 1.25, ease: 'easeInOut' }}
        />
        <section className="max-h-screen overflow-y-auto">
          <ul id="accordion-list">
            <Accordion
              title="劇本"
              content={
                <>
                  {screenplay.script.map((script) => (
                    <article
                      key={`${script.session}-${script.time}-${script.scene}`}
                      className="mb-8"
                    >
                      <p className="text-title font-noto-serif-tc font-bold">
                        {script.session}
                      </p>
                      <p className="text-title font-noto-serif-tc font-bold">
                        {script.time}
                      </p>
                      <p className="text-title font-noto-serif-tc font-bold">
                        {script.scene}
                      </p>
                      <ReactMarkdown className="markdown-preview mt-8 font-noto-serif-tc">
                        {script.content}
                      </ReactMarkdown>
                    </article>
                  ))}
                </>
              }
              open={panel === 'script'}
              onChange={() => setPanel('script')}
            />
            <Accordion
              title="編劇自白"
              content={
                <>
                  <p className="mb-4">{screenplay.confession}</p>
                  <div className="rounded-2xl bg-[#f0f1f3] p-4">
                    <h4 className="text-small-title mb-4 text-graphite">
                      作者
                    </h4>
                    {screenplay.screenwriters?.map(
                      ({ avatarId, username, displayName, id }) => (
                        <Link
                          className="flex items-center gap-x-2"
                          key={username}
                          to={`/screenwriters/${id}`}
                        >
                          <Avatar
                            avatarId={avatarId}
                            name={displayName}
                            size="medium"
                          />
                          <span className="text-small-title">{username}</span>
                        </Link>
                      )
                    )}
                  </div>
                </>
              }
              open={panel === 'confession'}
              onChange={() => setPanel('confession')}
            />
          </ul>
        </section>
      </main>
    </>
  )
}

export default ScreenplayPage
