import { useContext } from 'react'
import {
  useForm,
  SubmitHandler,
  UseFormWatch,
  UseFormRegister,
} from 'react-hook-form'
import { User as Profile } from '../../typings'
import AppContext from '../../context/AppContext'
import { TextArea, TextAreaProps } from './TextArea'
import ModalContext from '../../context/ModalContext'
import Stroke03 from '../../assets/strokes/Stroke03'

interface SocialNetworkTextAreaProps extends TextAreaProps {
  fieldName: keyof Profile
  watch: UseFormWatch<Profile>
  register: UseFormRegister<Profile>
}
interface ProfileFormProps {
  profile: Profile
}

const SocialNetworkTextArea = ({
  fieldName,
  watch,
  register,
  label,
  hintText,
  errors,
  maxLength,
}: SocialNetworkTextAreaProps) => (
  <TextArea
    fieldName={fieldName}
    label={label}
    hintText={hintText}
    register={register}
    watch={watch}
    errors={errors}
    maxLength={maxLength}
  />
)

const SocialNetworkForm = ({ profile }: ProfileFormProps) => {
  const { updateProfile } = useContext(AppContext)
  const { handleModal } = useContext(ModalContext)

  const {
    register,
    formState: { errors },
    handleSubmit,
    watch,
  } = useForm<Profile>({
    mode: 'onChange',
    defaultValues: profile,
  })

  const onSubmit: SubmitHandler<Profile> = (data) => {
    const updatedSocialNetworkData = {
      ...profile,
      ...data,
    }

    updateProfile(updatedSocialNetworkData).then((profileId) => {
      if (profileId) {
        handleModal(false)
      }
    })
  }

  return (
    <form onSubmit={handleSubmit(onSubmit)}>
      <section className="flex flex-col gap-10">
        <SocialNetworkTextArea
          fieldName="contactEmail"
          label="Email"
          hintText="其他人看你的Email"
          register={register}
          watch={watch}
          errors={errors}
          maxLength={50}
        />
        <SocialNetworkTextArea
          fieldName="facebook"
          label="Facebook"
          hintText="其他人看到你的Facebook"
          register={register}
          watch={watch}
          errors={errors}
          maxLength={50}
        />
        <SocialNetworkTextArea
          fieldName="instagram"
          label="Instagram"
          hintText="其他人看到你的Instagram"
          register={register}
          watch={watch}
          errors={errors}
          maxLength={50}
        />
        <SocialNetworkTextArea
          fieldName="youtube"
          label="YouTube"
          hintText="其他人看到你的YouTube"
          register={register}
          watch={watch}
          errors={errors}
          maxLength={50}
        />
        <div className="text-heading flex cursor-pointer items-center gap-x-1 bg-highlight02 bg-contain bg-no-repeat">
          <Stroke03 className="h-8 xl:h-10" />
          <input type="submit" value="儲存" />
        </div>
      </section>
    </form>
  )
}

export default SocialNetworkForm
