import { Link } from 'react-router-dom'
import { RouteNames, RoutePaths } from '../../enums/routeEnum'

export interface BreadcrumbProps {
  breadcrumbTrail?: string[]
}

type RouteStrings = keyof typeof RouteNames

export const Breadcrumb = ({ breadcrumbTrail }: BreadcrumbProps) => (
  <div className="flex gap-2">
    {breadcrumbTrail?.map((trail, i) => {
      const islastItem = i === breadcrumbTrail.length - 1

      if (!islastItem) {
        return (
          <>
            <Link key={trail} to={`/${RoutePaths[trail as RouteStrings]}`}>
              {RouteNames[trail as RouteStrings]}
            </Link>
            {'>'}
          </>
        )
      }

      return <span key={trail}>{RouteNames[trail as RouteStrings]}</span>
    })}
  </div>
)
