import { useContext, useState } from 'react'
import { useQuery } from '@tanstack/react-query'
import { NavLink, useNavigate, useSearchParams } from 'react-router-dom'
import { motion } from 'framer-motion'
import AppContext from '../../context/AppContext'
import { User as ScreenwriterEntry } from '../../typings'
import ListRow from '../List/ListRow'
import Paginate from '../Paginate/Paginate'
import Avatar from './Avatar'

interface ScreenwriterListProps {
  itemsPerPage: number
}

const ScreenwriterList = ({ itemsPerPage }: ScreenwriterListProps) => {
  const { fetchScreenwriterList } = useContext(AppContext)
  const [searchParams] = useSearchParams()
  const [currentPage, setCurrentPage] = useState(
    parseInt(searchParams.get('page') || '0', 10)
  )
  const navigate = useNavigate()

  const paginate = (pageNumber: number) => {
    setCurrentPage(pageNumber)
    searchParams.set('page', pageNumber.toString())
    navigate(`?page=${pageNumber}`)
  }

  const isComponentAnimationLoaded =
    sessionStorage.getItem('componentAnimation') === 'loaded'

  const { isLoading, isError, error, data, isPreviousData } = useQuery({
    queryKey: ['screenwriters', currentPage],
    queryFn: () => fetchScreenwriterList(currentPage, itemsPerPage),
    keepPreviousData: true,
  })

  if (!data) {
    return <>Loading.</>
  }

  if (currentPage > Math.ceil(data.totalCount / itemsPerPage) - 1) {
    paginate(0)
  }

  return (
    <>
      <section className="flex flex-col justify-between border-b border-graphite p-4 md:flex-row md:items-center">
        <h1 className="text-heading">{data.totalCount}位編劇</h1>
      </section>
      <motion.div
        className="hidden gap-x-4 border-y border-graphite py-2 md:grid md:grid-cols-12"
        initial={{ opacity: 0 }}
        animate={{ opacity: 100 }}
        transition={{ duration: 1.25, ease: 'easeOut', delay: 0.75 }}
      >
        <span className="text-small-title pl-4 md:col-span-3 xl:col-span-2">
          編劇
        </span>
        <span className="text-small-title md:col-span-3 xl:col-span-4">
          編劇簡介
        </span>
        <span className="text-small-title md:col-span-6 xl:col-span-6">
          作品數
        </span>
      </motion.div>
      <motion.figure
        className="block h-px w-full origin-left bg-black"
        initial={{ scaleX: 0 }}
        animate={{ scaleX: 1 }}
        transition={{ duration: 1.25, ease: 'easeInOut', delay: 0.5 }}
      />
      <div>
        {isLoading ? (
          <div>Loading...</div>
        ) : isError ? (
          <div>Error: {(error as any).message}</div>
        ) : (
          <ul>
            {data.data?.map(
              (
                {
                  id: screenwriterId,
                  avatarId,
                  displayName,
                  intro,
                  scriptCount,
                }: ScreenwriterEntry,
                i
              ) => (
                <ListRow
                  key={screenwriterId}
                  initial={!isComponentAnimationLoaded}
                  contentTransition={{
                    duration: 1.25,
                    ease: 'easeOut',
                    delay: 1 + (i % itemsPerPage) * 0.25,
                  }}
                  lineTransition={{
                    duration: 1.25,
                    ease: 'easeInOut',
                    delay: 0.75 + (i % itemsPerPage) * 0.25,
                  }}
                >
                  <NavLink
                    to={`/screenwriters/${screenwriterId}`}
                    className="grid cursor-pointer items-center gap-x-4 border-b border-graphite p-4 md:grid-cols-12 md:grid-rows-[156px] md:py-0 lg:grid-rows-[128px] xl:grid-rows-[106px]"
                  >
                    <span className="text-title flex h-full flex-wrap items-center gap-x-4 md:col-span-3 xl:col-span-2">
                      <Avatar avatarId={avatarId} name={displayName} />
                      <span className="shrink">{displayName}</span>
                    </span>
                    <span className="text-small-title md:col-span-3 xl:col-span-4">
                      {intro}
                    </span>
                    <span className="text-small-title md:col-span-6 xl:col-span-6">
                      {scriptCount}
                    </span>
                  </NavLink>
                </ListRow>
              )
            )}
          </ul>
        )}
      </div>
      <Paginate
        itemsPerPage={itemsPerPage}
        currentPage={currentPage}
        totalCount={data?.totalCount}
        paginate={paginate}
        isPreviousData={isPreviousData}
      />
    </>
  )
}

export default ScreenwriterList
